@import 'ScssHelpers/helpers_all';

.quote {
	background-color: $body_bg;

	@include bp($point_1, min) {
		padding: 12.6rem 12.6rem 10.6rem var(--gl-indent);
	}

	@include bp($point_1 - 1, $point_2) {
		padding: 12.8rem 5rem 10.7rem 8rem;
	}

	@include bp($point_3) {
		padding: 6.6rem 7.3rem 3.2rem 4.9rem;
	}
}

.quote_in {
	max-width: 107.2rem;
}

.quote__block {
	@include flex_block(row, wrap, flex-start, stretch, flex-start);

	@include bp($point_3) {
		flex-direction: column;
	}
}

.quote__block_img_w {
	display: block;

	@include bp($point_2, min) {
		width: 44.7%;
	}

	@include bp($point_3) {
		width: 117%;
		margin: 0 0 3.4rem -5rem;
	}
}

.quote__block_img {
	@extend %cover_img;
}

.quote__block_info {
	@include flex_block(row, wrap, flex-start, flex-start, center);

	@include bp($point_2, min) {
		width: 55.3%;
		padding: 5rem 0 5rem 10.8rem;
	}

	@include bp($point_1 - 1, $point_2) {
		padding: 5rem 0 5rem 5.8rem;
	}

	@include bp($point_3) {
		width: 100%;
	}
}

.quote__block_info_head {
	width: 100%;

	@include bp($point_2, min) {
		margin-bottom: 5rem;
	}

	@include bp($point_3) {
		margin-bottom: 3rem;
	}
}

.quote__block_info_head_in {
	@include flex_block(row, wrap, flex-start, baseline, flex-start);

}

.quote__block_info_title {
	font-weight: 800;
	letter-spacing: .01em;

	@include bp($point_2, min) {
		margin-right: 1.5rem;
		font-size: 3.8rem;
	}

	@include bp($point_3) {
		margin-right: 1rem;
		font-size: 2.4rem;
	}
}

.quote__block_info_label {
	font-weight: 800;
	line-height: 1.1;
	color: $gray_2;

	@include bp($point_2, min) {
		font-size: 2.9rem;
	}

	@include bp($point_3) {
		font-size: 2.2rem;
	}
}

.quote__block_info_social {
	@extend %transition_background;
	color: $white;

	background-color: $black;
	border-radius: 50%;

	@include bp($point_2, min) {
		width: 2.5rem;
		height: 2.5rem;

		margin-left: auto;
		padding: .7rem;
	}

	@include bp($point_3) {
		width: 1.8rem;
		height: 1.8rem;

		margin-left: .8rem;
		padding: .6rem;
	}

	&:hover,
	&:focus {
		background-color: lighten($black, 20%);
	}
}

.quote__block_info_text {
	@include bp($point_2, min) {
		margin-bottom: 4.2rem;
	}

	@include bp($point_3) {
		margin-bottom: 2rem;
	}
}

.quote__block_info_icon {
	position: absolute;
	left: 0;

	@include bp($point_2, min) {
		top: 1rem;

		width: 2.1rem;
		height: 1.4rem;

		margin: 0 3rem 1.2rem 0;
	}

	@include bp($point_3) {
		top: .4rem;

		width: 1.5rem;
		height: 1rem;

		margin: 0 1.2rem .5rem 0;
	}
}

.quote__block_info_text_in {
	position: relative;
	p {
		font-weight: 300;

		@include bp($point_2, min) {
			font-size: 3.8rem;
			text-indent: 5.4rem;
			letter-spacing: .01em;
			line-height: 1.18;
		}

		@include bp($point_3) {
			font-size: 2.1rem;
			text-indent: 3.1rem;
			letter-spacing: .01em;
			line-height: 1.34;
		}
	}
}

.quote__block_info_signature {
	display: block;

	@include bp($point_2, min) {
		width: 72%;
	}

	@include bp($point_3) {
		width: 60%;
		margin-left: auto;
	}
}

.quote__block_info_signature_img {
	@extend %cover_img;
}