@import "ScssHelpers/_helpers_all.scss";

.innovation_section {
	position: relative;

	background-color: $white_2;

	@include bp($point_2, min) {
		padding: 13rem 5rem 23.8rem 23.8rem;
	}

	@include bp($point_3) {
		padding: 5rem 7.2rem 5.6rem 5rem;
	}
}

.innovation_section__title {
	@extend %h3;

	position: relative;
	z-index: 5;

	text-transform: uppercase;
	text-indent: 4.4rem;

	@include bp($point_2, min) {
		text-indent: 4.4rem;
		line-height: 1.18;

		&:not(:last-child) {
			margin-bottom: 4rem;
		}
	}

	@include bp($point_3) {
		text-indent: 3rem;
		line-height: 1.25;

		&:not(:last-child) {
			margin-bottom: 4rem;
		}
	}

	//.body--safari_state & {
	//	line-height: 1.55;
	//}
}

.innovation_section__title_in {
}

.innovation_section__title_decor {
	position: absolute;
	top: 0;
	left: 0;

	background-color: $blue;

	@include bp($point_2, min) {
		width: 0.7rem;
		height: 2.7rem;
		margin-top: 1rem;
	}

	@include bp($point_3) {
		width: 0.5rem;
		height: 1.8rem;
		margin-top: 0.5rem;
	}

	.body--safari_state & {
		margin-top: 0 !important;
	}
}

.innovation_section_text {
	@extend %descr;

	position: relative;
	z-index: 5;

	max-width: 100.4rem;

	@include bp($point_3) {
		line-height: 1.33;
	}
}

.innovation_section__bg {
	position: absolute;
	bottom: 0;
	left: 52%;

	height: 100%;

	transform: translateX(-52%);
	z-index: 2;

	opacity: 0.05;

	@include bp($point_4, min) {
		width: 98rem;
	}

	@include bp($point_4 - 1) {
		width: 100%;
	}
}

.innovation_section__bg_1_decor,
.innovation_section__bg_2_decor {
	@extend %cover_img;
}

.innovation_section__bg_1_decor {
	@include bp($point_4 - 1) {
		display: none;
	}
}
.innovation_section__bg_2_decor {
	@include bp($point_4, min) {
		display: none;
	}
}
