%cover_img {
	display: block;
	width: 100%;
	height: 100%;

	object-fit: cover;
}

%contain_img {
	display: block;
	width: 100%;
	height: 100%;

	object-fit: contain;
}

%popup {
	@extend %global_flex_block_row_wrap_center_center;

	position: fixed;
	top: 0;
	left: 0;

	width: 100%;
	height: 100vh;
	padding: 2rem;

	overflow-y: auto;

	// background-color: rgba($black, .75);

	opacity: 0;
	pointer-events: none;
	transition: opacity .3s;// значение милисекунд должно совпадать с "const timeout" (300) в initPopup.js

	z-index: 1000;

	&.active_state {
		opacity: 1 !important;
		pointer-events: initial;
	}
}

%solutions_decor {
	position: relative;
	padding-top: 50%;
	width: 100%;
	height: 50%;
}

%solutions_decor_in {
	width: 100%;
	height: 200%;

	border-radius: 50%;
}
